#bio-text {
    font-size: 15pt;
    line-height: 1.9;
    color: #ffffff;
    text-align: center;
    padding: 0rem 1rem;
    display: inline-block;
}

.tab {
    display: inline-block;
    margin-left: 40px;
}

#photo-link {
    color: #2ee990;
}

#photo-link:hover {
    text-decoration: underline;
}

/* bio image */
.bio-photo {
    display: inline-block;
    position: relative;
    width: 22rem;
    height: 22rem;
    cursor: pointer;
    background: no-repeat 100% 50%;
    background-size: 120%;
    border-radius: 50%;
    transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
    box-shadow: 0px 0.5rem 1rem rgba(0, 0, 0, 0.75);
}
.bio-photo:hover {
    box-shadow: 0px 0.5rem 2rem rgba(0, 0, 0, 0.75);
    background-position: 0% 50%;
}
.bio-photo:hover .bio-hello {
    transform: rotate(-45deg) translate(0px) rotate(-315deg);
    background: #2ee99080;
    width: 22rem;
    letter-spacing: 1rem;
    line-height: 21rem;
    height: 22rem;
    margin-top: -11rem;
    margin-left: -11rem;
    transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1), letter-spacing 1.5s ease-out;
}
.bio-photo .bio-hello {
    display: block;
    position: absolute;
    width: 10rem;
    height: 10rem;
    line-height: 10rem;
    left: 50%;
    top: 50%;
    margin-top: -5rem;
    margin-left: -5rem;
    font-size: 2rem;
    font-family: 'Poiret One', cursive;
    text-align: center;
    color: #fff;
    font-weight: bold;
    border-radius: 50%;
    background: #2ee990;
    transform: rotate(-180deg) translate(-10rem) rotate(180deg);
    box-shadow: 0px 0.5rem 1rem rgba(0, 0, 0, 0.5);
    transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

@media screen and (max-width: 992px) {
    .bio-photo-div {
        text-align: center;
        width: 100%;
    }
    #bio-text {
        display: block;
        width: 100%;
    }
}