/* Button Links ----------------- */
*, *::after, *::before {
  box-sizing: border-box;
}

.project-btns {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}

.project-btns {
  position: relative;
  border: none;
  font-size: 17px;
  transition: color 0.5s, transform 0.2s, background-color 0.2s;
  outline: none;
  border: 2px solid transparent;
  width: 100%;
  padding: .5rem 0rem;
  margin-top: 1rem;
}

.project-btns:active {
  transform: translateY(3px);
}

.project-btns::after, .project-btns::before {
  border-radius: 2px;
}

.shrink-border {
  background-color: transparent;
  color: #2ee990;
}

.shrink-border:hover {
  background-color: transparent;
  box-shadow: none;
  color: #e6e6e6;
}

.shrink-border::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid #2ee990;
  transition: opacity 0.3s, border 0.3s;
}

.shrink-border:hover::before {
  opacity: 0;
}

.shrink-border::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 2px solid #e6e6e6;
  opacity: 0;
  transform: scaleX(1.1) scaleY(1.3);
  transition: transform 0.3s, opacity 0.3s;
}

.shrink-border:hover::after {
  opacity: 1;
  transform: scaleX(1) scaleY(1);
}

/* Messing with corner frame ---------------- */
.corner-frame {
  position:relative;
  width:100%;
  height:100%;
  margin-bottom: 1rem;
}

.corner-frame:before, .corner-frame:after, .corner-frame>:first-child:before, .corner-frame>:first-child:after {
  position:absolute;
  width:20px; height: 20px;
  border-color: #2ee990;
  border-style:solid; 
  content: ' ';
}

.corner-frame:before {
  top:0;left:0;border-width: 1px 0 0 1px
}

.corner-frame:after {
  top:0;right:0;border-width: 1px 1px 0 0
}

.corner-frame>:first-child:before {
  bottom:0;right:0;border-width: 0 1px 1px 0
}

.corner-frame>:first-child:after {
  bottom:0;left:0;border-width: 0 0 1px 1px
}
.wrap {
